import React, { FC } from "react";

import { toLocaleString } from "utils";

import { ImpressiveWinsInteface } from "pages/Home/types/homeInfo";
import winsIcon from "assets/homepage/stats/wow-wins.png";
import { LinkComponent, ListSection } from "../../components";
import { originGames } from "../..";

interface Props {
  data?: ImpressiveWinsInteface[];
}

export const ImpressiveWins: FC<Props> = ({ data }) => {
  return (
    <ListSection
      logoUrl={winsIcon}
      title="Top Wins"
      content={data?.map(
        ({ game_id, image_url, net_profit, title, user_name }) => {
          const gameUrl: string =
              originGames[game_id]?.link || `/slot/${game_id}`,
            imgLink: string = originGames[game_id]?.imgUrl || image_url;

          return {
            title: (
              <LinkComponent to={`/modal/profile/${user_name}`}>
                {user_name}
              </LinkComponent>
            ),
            value: ["Won: ", `$${toLocaleString(net_profit, 2)}`],
            adornment: <LinkComponent to={gameUrl}>{title}</LinkComponent>,

            image_url: imgLink,
            key: game_id,
            url: gameUrl,
          };
        },
      )}
    />
  );
};
