import React, { FC, useRef } from "react";

import { OptimizedSlider, SliderValues, useAutoScroll } from "components";

import { StatItem } from "../";
import { Banner } from "./type";
import { SectionContent, Arrows } from "./components";

interface Props {
  logoUrl: string;
  title: string;
  banners: Banner[];
}

export const SlideSection: FC<Props> = ({ logoUrl, title, banners }) => {
  const sliderRef = useRef<SliderValues | null>(null);
  const increment = () => {
    sliderRef.current?.increment();
  };
  const decrement = () => {
    sliderRef.current?.decrement();
  };
  const { start, stop } = useAutoScroll(increment, 3000);
  return (
    <StatItem image={logoUrl} title={title}>
      <Arrows leftOnClick={decrement} rightOnClick={increment} />
      <OptimizedSlider
        cardsAmount={1}
        rows={2}
        dataLength={banners.length}
        ref={sliderRef}
        onMouseOver={stop}
        onMouseLeave={start}>
        {banners.length > 0 &&
          banners.map((banner) => (
            <div>
              <SectionContent {...banner} />
            </div>
          ))}
      </OptimizedSlider>
    </StatItem>
  );
};
