import React, { DetailedHTMLProps, FC, ImgHTMLAttributes } from "react";
import clsx from "clsx";

import css from "./style.module.scss";
import { Link } from "react-router-dom";
import { createLinkParamsFromString } from "utils";

interface Props
  extends DetailedHTMLProps<
    ImgHTMLAttributes<HTMLImageElement>,
    HTMLImageElement
  > {
  url?: string;
}

export const MiniBanner: FC<Props> = ({ className, url, src, ...rest }) => {
  return (
    <Link to={createLinkParamsFromString(url)} className={css.link}>
      <img className={clsx(className, css.img)} src={src} {...rest} />
    </Link>
  );
};
