import React, { CSSProperties, FC } from "react";

import { TextData, TextElementType } from "../TextData";
import { MiniBanner } from "../MiniBanner";
import StatItem from "../StatItem";
import css from "./style.module.scss";

interface Props {
  logoUrl: string;
  title: string;

  content?: {
    image_url: string;
    title?: TextElementType;
    value?: [TextElementType, TextElementType];
    adornment?: TextElementType;
    key?: string;
    url?: string;
  }[];
}

export const ListSection: FC<Props> = ({ content, logoUrl, title }) => {
  return (
    <StatItem image={logoUrl} title={title}>
      {content?.map(({ image_url, title, value, adornment, key, url }, i) => (
        <div
          className={css.content}
          key={key}
          style={
            {
              "--mini-banner-size": "52px",
            } as CSSProperties
          }>
          <MiniBanner src={image_url} url={url} />

          <TextData title={title} value={value} adornment={adornment} />
        </div>
      ))}
    </StatItem>
  );
};
