import React, { FC } from "react";
import clsx from "clsx";

import { Button } from "components";

import { ReactComponent as ArrowRight } from "assets/icons/chevron-right.svg";
import { ReactComponent as ArrowLeft } from "assets/icons/chevron-left.svg";
import { ArrowProps, ArrowWrapperProps } from "../../type";
import css from "./style.module.scss";

export const Arrow: FC<ArrowProps> = ({ direction, disabled, ...rest }) => (
  <Button
    {...rest}
    className={clsx(
      css.arrow,
      direction === "left" ? css.arrow_left : css.arrow_right,
      disabled && css.arrow_disabled,
    )}>
    {direction === "left" ? <ArrowLeft /> : <ArrowRight />}
  </Button>
);

export const Arrows: FC<ArrowWrapperProps> = ({
  disabled,
  leftOnClick,
  rightOnClick,
}) => {
  return (
    <div className={css.arrowsWrapper}>
      <Arrow direction="right" onClick={rightOnClick} disabled={disabled} />
      <Arrow direction="left" onClick={leftOnClick} disabled={disabled} />
    </div>
  );
};
