import React, { FC } from "react";
import { IGameInterface } from "pages/Home/types/homeInfo";
import TrendingIconURL from "assets/homepage/stats/trending.png";
import { LinkComponent, SlideSection } from "../../components";
import { originGames } from "../..";

interface Props {
  data?: IGameInterface[];
}

export const TrendingGames: FC<Props> = ({ data = [] }) => {
  return (
    <SlideSection
      banners={data.map(({ id, image_url, name, payout, provider }, i) => {
        const gameUrl: string = originGames[id]?.link || `/slot/${id}`,
          imgLink: string = originGames[id]?.imgUrl || image_url;

        return {
          url: gameUrl,
          image_url: imgLink,
          key: `${id}_${i}`,
          title: <LinkComponent to={gameUrl}>{name}</LinkComponent>,
          value: ["RTP: ", payout],
        };
      })}
      title="Trending"
      logoUrl={TrendingIconURL}
    />
  );
};
