import React, { FC } from "react";
import { toLocaleString } from "utils";

import { IProfitableGamesInterface } from "pages/Home/types/homeInfo";
import winnersIcon from "assets/homepage/stats/top-winnings.png";
import { LinkComponent, ListSection } from "../../components";
import { originGames } from "../..";

interface Props {
  data?: IProfitableGamesInterface[];
}

export const TopWinnings: FC<Props> = ({ data }) => {
  return (
    <ListSection
      logoUrl={winnersIcon}
      title="Top Games"
      content={data?.map(({ game_id, gross_profit, image_url, title }) => {
        const gameUrl: string =
            originGames[game_id]?.link || `/slot/${game_id}`,
          imgLink: string = originGames[game_id]?.imgUrl || image_url;

        return {
          key: game_id,
          value: ["Profit: ", `$${toLocaleString(gross_profit, 2)}`],
          adornment: <LinkComponent to={gameUrl}>{title}</LinkComponent>,
          url: gameUrl,
          image_url: imgLink,
        };
      })}
    />
  );
};
