import React, { FC, useLayoutEffect, useRef } from "react";
import { useWindowSize } from "providers";
import { sleep } from "utils";

import {
  ImpressiveWins,
  NewGames,
  TopWinnings,
  TrendingGames,
} from "./sections";
import { StatProps, StatSizes } from "./types";
import css from "./style.module.scss";

const cardWidth = parseInt(css.cardWidth);

const Content: FC<StatProps> = ({
  impressiveWins = [],
  topWinnings = [],
  recentlyAdded = [],
  trending = [],
}) => {
  const { isMobile, width } = useWindowSize();
  const container = useRef<HTMLDivElement | null>(null);

  const calculateCards = async (node = container.current) => {
    if (!node) {
      return;
    }

    await sleep(0);

    const { offsetWidth, children, style } = node;
    const elementsList = [...children] as HTMLDivElement[];

    const margin = 5;
    const newCardSize = offsetWidth / 2 - margin * 1.5;

    const scaleParameter = Math.min(newCardSize / cardWidth, 1);
    const scaleDifference = cardWidth - cardWidth * scaleParameter;

    const sizes = elementsList.reduce<StatSizes>(
      (acm, { offsetHeight: height, style: cardStyle }, index) => {
        if (isMobile) {
          cardStyle.transform = `scale(${scaleParameter})`;
          cardStyle.position = "absolute";

          if (index % 2) {
            cardStyle.right = `-${scaleDifference / 2}px`;
            cardStyle.left = "";

            acm.heightValues.push(height);
            acm.sumHeight += height + margin * 2;
          } else {
            cardStyle.right = "";
            cardStyle.left = `-${scaleDifference / 2}px`;
          }

          const actualRow = Math.floor(index / 2);
          const prevHeight = acm.heightValues[actualRow - 1] || 0;

          const top = `${
            actualRow * (prevHeight * scaleParameter) -
            scaleDifference / 2 +
            margin * actualRow
          }px`;

          cardStyle.top = top;
        } else {
          cardStyle.transform = "";
          cardStyle.position = "";
        }

        return acm;
      },
      {
        heightValues: [],
        sumHeight: 0,
      },
    );

    if (isMobile) {
      style.height = `${sizes.sumHeight * scaleParameter}px`;
    } else {
      style.height = "";
    }
  };

  useLayoutEffect(() => {
    calculateCards();
  }, [isMobile, width, impressiveWins, topWinnings, recentlyAdded, trending]);

  return (
    <div
      className={css.container}
      ref={(node) => {
        calculateCards(node);
        container.current = node;
      }}>
      <ImpressiveWins data={impressiveWins} />
      <TopWinnings data={topWinnings} />
      <NewGames data={recentlyAdded} />
      <TrendingGames data={trending} />
    </div>
  );
};

export default Content;
