import clsx from "clsx";
import React, { DetailedHTMLProps, FC, HTMLAttributes } from "react";
import css from "./style.module.scss";

export type TextElementType = string | number | JSX.Element;

interface Props
  extends Omit<
    DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>,
    "title" | "value" | "adornment"
  > {
  title?: TextElementType;
  value?: [TextElementType, TextElementType];
  adornment?: TextElementType;
}

export const TextData: FC<Props> = ({
  value: [valueName, valueData] = [" ", " "],
  title = " ",
  adornment = " ",

  className,
  ...rest
}) => {
  return (
    <div className={clsx(css.root, className)} {...rest}>
      <span className={css.defaultText}>{title}</span>

      <span className={css.valueTextWrapper}>
        <span className={css.valueTextTitle}>{valueName}</span>
        <span className={css.value}>{valueData}</span>
      </span>
      <span className={css.defaultText}>{adornment}</span>
    </div>
  );
};
