import React, { CSSProperties, FC } from "react";
import { MiniBanner, TextData } from "../../../";
import { Banner } from "../../type";
import css from "./style.module.scss";

export const SectionContent: FC<Banner> = ({
  image_url,
  adornment,
  key,
  title,
  value,
  url,
}) => (
  <div
    className={css.content}
    key={key}
    style={
      {
        "--mini-banner-size": "83px",
      } as CSSProperties
    }>
    <MiniBanner src={image_url} className={css.bigImg} url={url} />

    <TextData
      title={title}
      value={value}
      adornment={adornment}
      className={css.text}
    />
  </div>
);
